import React from 'react';
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet'
const articleData = require('./../locations.json');

const styles = {
  titleImage: {
    marginLeft: "10%", 
    marginRight: "10%", 
    textAlign: "center", 
    backgroundSize: 'cover', 
    
  },
  titleText: {
  },
  hide: {
    visibility: "hidden",
    width: "100%", 
    height: "50%"
  },
  bodyText: { 
    marginLeft: "15%", 
    marginRight: "15%"
  },
  map: {
    height: "200px",
  }
}


function ArticlePage(props) {

  console.log("props")
  console.log(props)

  const article = (articleData.filter(
    elem => elem.location === props.locationTracker
  ))[0];
  console.log(article)

  let addLocation = (e) => {
    e.preventDefault()
    props.updateList((oldList) => [...oldList,article.location])
    console.log("CLICKED IT")
    console.log(props)
  }

  return (
    <div> 
      <img src={article['image']['url']}alt='cute shibe' />
      <h1 style={styles.titleText}> {article.location}</h1>
      <div style={styles.bodyText}>
        {article.cityState}
        <button onClick={addLocation}>ADD</button>
        <h2> {article.blurb} </h2>
          { article.description}
      </div>

      <MapContainer style={styles.map} center={[40.712, -74.0060]} zoom={10} scrollWheelZoom={false} tap={false}>
      <TileLayer
        attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
      />
      
      <Marker position={[article.latitude, article.longitude]}>
        <Popup>
          {article.location}
        </Popup>
      </Marker>
    </MapContainer>

    </div>
  );

}


export default ArticlePage;