import React from 'react';

const styles = {
    container: {
      display: "flex", 
      flexDirection: "row",  
      justifyContent: "space-evenly",
      alignItems: "center",
      border: "black 1px solid",
      marginLeft: "10%",
      marginRight: "10%",
      marginTop: "1%"
    },
    imageBox: {
      flex: 1,
      paddingLeft: "5%"
    }, 
    articlePreviewBox: { 
      flex: 3,
      padding: "2%"
    },
    imageCard: {
      maxWidth: "100%"
    }
}


function ArticleCard(props) {
  console.log(props)

  let addLocation = (e) => {
    e.preventDefault()
    props.updatePage("locationPage")
    props.updateLocation(props.location)
  }

  return (
    <div className="locationCard" > 
      <div className="image">
        <img className="imageCard" src={props['image']['url']} alt={props['image']['alt']}/>
      </div>  
      <div className="descriptionBox" key={props.location} >
        <h2> {props.location} </h2>
        <h4> {props.cityState} </h4>
        <p> {props.blurb} </p>
        <a href="/" onClick={addLocation}>read more</a>
      </div>
    </div> 
  );
}

export default ArticleCard;
