import React, {useState} from 'react';
// import {BrowserRouter as Router, Route} from 'react-router-dom';

import ArticleMain from './articleMain.js';
import ArticlePage from './articlePage.js';
import Schedule from './schedule.js';
import Header from './Header.js';


const styles = {
  container: {
    textAlign: "center", 
  }
}




function App(props) {
  const data = props.data; 
  const [locationList, updateList] = useState(["forest","asdf"]);
  const [pageTracker, updatePage] = useState("explore");
  const [locationTracker, updateLocation] = useState("forest");

  function pagePicker(){
    switch(pageTracker){
      // case "explore":
      //   return <ArticleMain data={data} locationList={locationList} updatePage={updatePage} updateLocation={updateLocation}/>
      case "locationPage":
        return <ArticlePage locationList={locationList} updateList={updateList} locationTracker={locationTracker}/>
      case "schedule":
        return <Schedule key="schedule" locationList={locationList} updateList={updateList} updatePage={updatePage} updateLocation={updateLocation}/>
      default:
        return <ArticleMain data={data} locationList={locationList} updatePage={updatePage} updateLocation={updateLocation}/>
    }
  }

  return (
  <div className='App' style={styles.container}>
    <Header updatePage={updatePage}/>
    {pagePicker()}

  	{/* <Router>
  		<Route
      exact path='/'
      render={(props) => <ArticleMain {...props} data={data} locationList={locationList}/>}
      />
      <Route 
      exact path='/location/:location'
      render={(props) => <ArticlePage {...props} locationList={locationList} updateList={updateList}/>}
      />
      <Route 
      exact path='/schedule'
      render={(props) => <Schedule {...props} locationList={locationList} updateList={updateList}/>}
      />
  	</Router> */}
  </div>
  );
}

export default App;
